import * as Immutable from "immutable";

export function conversationStates(
  state = Immutable.fromJS({
    isFetching: false,
    error: "",
    change: false,
    lastMsg: {
      msg: "",
      selects: [],
      buttons: []
    }
  }),
  action
) {
  switch (action.type) {
    case "GET_CONVERSATION_START":
      return state.set("isFetching", true);
    case "GET_CONVERSATION_END":
      return state.withMutations(map => {
        map.set("isFetching", false).set("error", false);
      });
    case "GET_CONVERSATION_ERROR":
      return state.withMutations(map => {
        map.set("isFetching", false).set("error", action.error);
      });
    case "UPDATE_CONVERSATION":
      return state.withMutations(map => {
        map.setIn(["lastMsg","msg"], action.data);
        map.set("change", true);
      });
    case "UPDATE_CONVERSATION_END":
      return state.withMutations(map => {
        map.set("change", false);
      });
    default:
      return state;
  }
}



