import React, { Component } from "react";
import IsFetching from "../modules/is-fetching";
import Conversation from "./conversation";
// import PropTypes from "prop-types";

export default class Conversations extends Component {
  constructor(props) {
    super(props);
    // this.callAsyncData();
    this.fillConversation = this.fillConversation.bind(this);
  }

  componentDidMount(){
    this.props.updateConversationEnd();
  }
  callAsyncData() {
    //this.props.getAyuda();
  }

  fillConversation(
    conversationStates,
    avatar,
    msg,
    updateConversationEnd,
    conversationsStates
  ) {

    if (conversationStates.get("change")) {
      return(
        conversationsStates.get("conversations").map((map, i) => {
          const conversation = map;
          if (i + 1 === conversationsStates.get("conversations").size) {//Si es la ltima conversa, pintar la clase para lal animacion
            return (
              <Conversation
                key={i}
                avatar={avatar}
                msg={conversation}
                updateConversationEnd={updateConversationEnd}
                last={true}
                send={conversation}
              />
            );
          }else{
            return (
              <Conversation
                key={i}
                avatar={avatar}
                msg={conversation}
                updateConversationEnd={updateConversationEnd}
                last={false}
                send={conversation}
              />
            );
          }
        })
      )
    } else {
      return <div />;
    }
  }

  render() {
    const {
        conversationsStates,
        customParamsStates,
        conversationStates
      } = this.props,
      avatar = customParamsStates.getIn(["customParams", "avatar"]),
      msg = conversationStates.getIn(["lastMsg", "msg"]);

    return (
      <IsFetching
        isFetching={conversationsStates.get("isFetching")}
        showChildren={true}
      >
        <section className="conversation-holder box-wrapp" data-conversation="">
          {this.fillConversation(
            conversationStates,
            avatar,
            msg,
            this.props.updateConversationEnd,
            conversationsStates
          )}
        </section>
      </IsFetching>
    );
  }
}

