import React, { Component } from "react";
// import PropTypes from "prop-types";

export default class BtnHelp extends Component {
  constructor(props) {
    super(props);
    this.toggleHelper = this.toggleHelper.bind(this);
  }

  toggleHelper(e) {
    const assistant = document.getElementsByClassName("assitant-helper")[0],
      button = document.getElementById("button-helper"),
      // button = document.getElementById('button-helper'),
      conversation = document.getElementsByClassName("conversation-holder")[0];
    let activeClass = assistant.classList[1];

    // Si hay abierta alguna modal
    if (document.getElementsByClassName("show")[0] !== undefined) {
      button.classList.add("disabled");
      activeClass = assistant.classList[1];
    }

    // Si el botn est deshabilitado
    if (button.classList.contains("disabled")) {
      if (!this.onTransitionHelp) {
        document
          .getElementsByClassName("bloqueo")[0]
          .classList.add("help-bloqued");
        document.getElementsByClassName("bloqueo")[0].innerHTML =
          "Debes concluir la interaccin para usar la ayuda";
        this.onTransitionHelp = true;
        setTimeout(() => {
          document
            .getElementsByClassName("bloqueo")[0]
            .classList.remove("help-bloqued");
          this.onTransitionHelp = false;
        }, 3000);
      }
      return false;
    }

    if (activeClass === undefined) {
      assistant.classList.add("active");
      button.classList.add("active");
      conversation.classList.add("active");

      if (document.getElementsByClassName("loader").length > 0)
        document
          .getElementsByClassName("loader")[0]
          .css({ top: "calc(-50vh - 30px)" });
      // iframe.classList.add('chico');
    } else {
      setTimeout(() => {
        if (document.getElementsByClassName("loader").length > 0)
          document.getElementsByClassName("loader")[0].css({ top: "initial" });
        assistant.classList.remove("active");
        button.classList.remove("active");
        conversation.classList.remove("active");
        const activos = document.getElementsByClassName("assistant-title");
        if (activos.length > 0) {
          for (let i = 0; i < activos.length; i++) {
            const element = activos[i];
            if (element.classList.contains("active")) element.click();
          }
        }
        // iframe.classList.remove('chico');
      }, 100);
    }
  }

  render() {
    return (
        <div id="button-helper" onClick={this.toggleHelper}>
          Ayuda
        </div>
      )
  }
}

