//CONVERSATION
export function updateConversation(data) {
  return function action(dispatch) {
    dispatch({ type: "UPDATE_CONVERSATION", data });
    dispatch({type:"PUSH_CONVERSATION", data });
  };
}

export function updateConversationEnd() {
  return function action(dispatch) {
    dispatch({ type: "UPDATE_CONVERSATION_END" });
  };
}

