import React, { Component } from "react";
import IsFetching from "../modules/is-fetching";

export default class Conversation extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    debugger;
    if (this.props.last) {
      return (
        <div className={"conversation-bubble animated-av fadeInUp-av "+this.props.send.get('send')}>
          <img
            className="rounded-img response-img"
            src={this.props.avatar}
            alt="Respuesta"
          />
          <div className="bubble">
            <span class="bubble-text"> {this.props.msg.get('msg')} </span>
          </div>
        </div>
      );
    } else {
      return (
        <div className={"conversation-bubble "+this.props.send.get('send')}>
          <img
            className="rounded-img response-img"
            src={this.props.avatar}
            alt="Respuesta"
          />
          <div className="bubble">
            <span class="bubble-text"> {this.props.msg.get('msg')} </span>
          </div>
        </div>
      );
    }
  }
}



